import { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Typography, IconButton, Stack, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Button } from '@mui/material';
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import { EditorState, convertFromRaw } from 'draft-js';
import DraftJsWysiwyg from './Draft-js-wysiwyg/Draft-js-wysiwyg';
import { SettingsContext } from '../../context/SettingsContext'; 
import { useUser } from '../../context/UserContext'; 
import BlogService from '../../services/BlogService';

const BlogViewer = ({ blogID, blogData }) => {
  const { themeMode, language } = useContext(SettingsContext); 
  const { userState } = useUser();
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false); 
  const [visibilityDialogOpen, setVisibilityDialogOpen] = useState(false);
  const [toggleLoading, setToggleLoading] = useState(false);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate(); 

  if (!blogData) {
    return null;
  }

  const title = language === "cn" ? blogData.cn_title : blogData.en_title;
  const content = language === "cn" ? blogData.cn_content : blogData.en_content;
  const { author, publish_date, last_updated_date } = blogData;

  let editorState;

  try {
    // 替换成单个反斜杠而不是移除
    let processedContent = content.replace(/\\/g, '');
    
    // 验证是否为有效的 JSON
    const parsedContent = JSON.parse(processedContent);
    if (!parsedContent) {
        throw new Error('Empty content');
    }
    
    const rawContent = convertFromRaw(parsedContent);
    editorState = EditorState.createWithContent(rawContent);
  } catch (error) {
    console.error("Error processing editor content:", error);
    editorState = EditorState.createEmpty();
  }

  const handleDeleteDialogOpen = () => {
    setDeleteDialogOpen(true);
  };

  const handleDeleteDialogClose = () => {
    setDeleteDialogOpen(false);
  };

  const handleVisibilityDialogOpen = () => {
    setVisibilityDialogOpen(true);
  };

  const handleVisibilityDialogClose = () => {
    setVisibilityDialogOpen(false);
  };

  const handleDelete = async () => {
    setLoading(true);
    try {
      await BlogService.deleteBlog(blogID, userState.access);
      setLoading(false);
      setDeleteDialogOpen(false);
      navigate('/blogs'); 
    } catch (error) {
      console.error("Error deleting blog", error);
      setLoading(false);
    }
  };

  const handleEdit = () => {
    navigate(`/editblog/${blogID}`);
  };

  const handleToggleVisibility = async () => {
    setToggleLoading(true);
    try {
      await BlogService.toggleVisibility(blogID, userState.access);
      blogData.visible = !blogData.visible;
      setToggleLoading(false);
      setVisibilityDialogOpen(false);
    } catch (error) {
      console.error("Error toggling visibility", error);
      setToggleLoading(false);
    }
  };

  function formatDate(dateStr) {
    const date = new Date(dateStr);
    if (language === 'cn') {
      return date.toLocaleDateString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
      });
    } else {
      const options = { day: '2-digit', month: 'short', year: 'numeric' };
      const formatted = date.toLocaleDateString('en-GB', options);
      return formatted;
    }
  }

  // 获取可见性状态的文本
  const getVisibilityStatusText = () => {
    if (language === 'cn') {
      return blogData.visible ? '公开' : '隐藏';
    } else {
      return blogData.visible ? 'visible' : 'hidden';
    }
  };

  const getVisibilityActionText = () => {
    if (language === 'cn') {
      return blogData.visible ? '隐藏' : '公开';
    } else {
      return blogData.visible ? 'hide' : 'make visible';
    }
  };

  return (
    <Box
      sx={{
        maxWidth: '850px',
        width: '100%',
        p: "3%",
        overflow: 'auto',
      }}
    >
      <Typography variant="h4" sx={{ mb: 2, fontWeight: 'bold', fontFamily: 'Georgia, serif' }}>
        {title}
      </Typography>
      
      <Stack direction="row" alignItems="center" justifyContent="space-between" sx={{ mb: 4 }}>
        <Typography variant="body2" color="text.secondary">
          {language === "cn" ? (
            <>发布日期: {formatDate(publish_date)} | 更新日期: {formatDate(last_updated_date)}</>
          ) : (
            <>Published: {formatDate(publish_date)} | Last Updated: {formatDate(last_updated_date)}</>
          )}
        </Typography>
        {userState.is_staff && (
          <Stack direction="row" spacing={0}>
            <IconButton
              size="small"
              color="primary"
              onClick={handleVisibilityDialogOpen}
              disabled={toggleLoading}
            >
              {blogData.visible ? <VisibilityIcon /> : <VisibilityOffIcon />}
            </IconButton>
            <IconButton
              size="small"
              color="secondary"
              onClick={handleEdit}
            >
              <EditOutlinedIcon />
            </IconButton>
            <IconButton
              size="small"
              color="error"
              onClick={handleDeleteDialogOpen}
            >
              <DeleteForeverOutlinedIcon />
            </IconButton>
          </Stack>
        )}
      </Stack>

      {/* 使用统一的编辑器组件，只读模式 */}
      <DraftJsWysiwyg
        editorState={editorState}
        readOnly={true}
      />

      {/* 删除确认对话框 */}
      <Dialog
        open={deleteDialogOpen}
        onClose={handleDeleteDialogClose}
      >
        <DialogTitle>{language === 'cn' ? '确认删除' : 'Confirm Delete'}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            {language === 'cn' 
              ? '您确定要删除这篇博客吗？此操作无法撤销。'
              : 'Are you sure you want to delete this blog? This action cannot be undone.'}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDeleteDialogClose} disabled={loading}>
            {language === 'cn' ? '取消' : 'Cancel'}
          </Button>
          <Button onClick={handleDelete} color="secondary" disabled={loading}>
            {loading ? (language === 'cn' ? '正在删除...' : 'Deleting...') : (language === 'cn' ? '删除' : 'Delete')}
          </Button>
        </DialogActions>
      </Dialog>

      {/* 可见性确认对话框 */}
      <Dialog
        open={visibilityDialogOpen}
        onClose={handleVisibilityDialogClose}
      >
        <DialogTitle>
          {language === 'cn' ? '确认更改可见性' : 'Confirm Visibility Change'}
        </DialogTitle>
        <DialogContent>
          <DialogContentText style={{ whiteSpace: "pre-line" }}>
            {language === 'cn' 
              ? `您确定要将这篇博客${getVisibilityActionText()}吗？\n当前状态：${getVisibilityStatusText()}。`
              : `Are you sure you want to ${getVisibilityActionText()} this blog?\nCurrent status: ${getVisibilityStatusText()}.`}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleVisibilityDialogClose} disabled={toggleLoading}>
            {language === 'cn' ? '取消' : 'Cancel'}
          </Button>
          <Button onClick={handleToggleVisibility} color="primary" disabled={toggleLoading}>
            {toggleLoading 
              ? (language === 'cn' ? '正在更改...' : 'Changing...') 
              : (language === 'cn' ? '确认' : 'Confirm')}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default BlogViewer;