import { useContext, useEffect, useState} from 'react';
import { useParams, useSearchParams } from 'react-router-dom';
import { Box, Typography, Snackbar, Alert } from '@mui/material';
import BlogService from '../../services/BlogService';
import CustomAppBar from '../../components/shared-components/AppBar/AppBar';
import BlogViewer from '../../components/blog-components/BlogViewer';
import { SettingsContext } from '../../context/SettingsContext';
import { useUser } from '../../context/UserContext';
import Footer from '../../components/shared-components/Footer';
import { motion } from 'framer-motion';

const fadeInVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0, transition: { duration: 0.5 } },
};

const BlogPage = () => {
  const { blogID } = useParams();
  const [searchParams, setSearchParams] = useSearchParams();
  const { language } = useContext(SettingsContext);
  const { userState } = useUser();

  const [blogData, setBlogData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  // Snackbar 状态
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");

  // 检查 URL 参数
  useEffect(() => {
    const updated = searchParams.get('updated');
    if (updated === 'true') {
      setSnackbarMessage(language === "cn" ? "博客已成功更新！" : "Blog updated successfully!");
      setSnackbarOpen(true);
      
      // 清除 URL 参数，避免刷新页面时重复显示
      searchParams.delete('updated');
      setSearchParams(searchParams, { replace: true });
    }
  }, [searchParams, setSearchParams, language]);

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  useEffect(() => {
    const fetchBlog = async () => {
      try {
        const data = await BlogService.getBlog(blogID, userState.access);
        setBlogData(data);
      } catch (err) {
        if (language === "cn") {
          setError("404 \n 服务器访问失败 \n 无法加载博客");
        } else {
          setError("404 \n Interal Server Error \n Fail to load blog data");
        }
        
      } finally {
        setLoading(false);
      }
    };

    fetchBlog();
  }, [blogID, language]);

  if (loading) {
    return (
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          height: '100vh',
        }}
      >
        <Typography variant="h6" align="center" mt={16}>
            {/* {language === 'cn' ? '正在加载内容...' : 'Loading content...'} */}
        </Typography>
        {/* <CircularProgress /> */}
      </Box>
    );
  }

  if (error) {
    return (
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          // alignItems: 'center',
          height: '100vh',
        }}
      >
        <CustomAppBar />
        <Typography color="error" variant="h6" align="center" mt={16} style={{ whiteSpace: "pre-line" }}>
          {error}
        </Typography>
      </Box>
    );
  }

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      {/* 上部导航栏 */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <CustomAppBar />
      </Box>

      {/* BlogView 内容区域 */}
      <motion.div initial="hidden" animate="visible" variants={fadeInVariants}>
      <Box
        sx={{
          flex: 1,
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'flex-start',
          mt: 12,
          px: { xs: 2, sm: 4, md: 6 },
          overflow: 'auto',
        }}
      >
        <BlogViewer blogID={blogID} blogData={blogData}/>
      </Box>
      </motion.div>
      
      {/* 下部 Footer */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          backgroundColor: 'background.default',
          borderTop: (theme) => ({
            xs: 'none', // 在 xs 屏幕无边框
            md: `1px solid ${theme.palette.divider}`, // 在 md 屏幕及以上显示边框
          }),
          maxWidth: (theme) => theme.breakpoints.values.lg, // 限制最大宽度为 lg
          width: '100%', // 确保宽度是 100% 的父元素宽度
          mx: 'auto', // 水平居中
        }}
      >
        <Footer />
      </Box>

      {/* Success Snackbar */}
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={1000}
        onClose={handleSnackbarClose}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert 
          severity="success" 
          sx={{ width: "100%" }}
        >
          {snackbarMessage}
        </Alert>
      </Snackbar>
      
    </Box>
  );
};

export default BlogPage;